@import "../utils/variables";

.projects {
    margin: 90px 0 90px 0;
    h2 {
        margin: 0;
        padding: 0;
        font-size: 40px;
        font-weight: 600;
        @media (max-width: 768px) {
            font-size: 30px;
        }
        @media (max-width: 480px) {
            font-size: 25px;
        }
    }
    a {
        text-decoration: none;
        color: $light-main-color;
    }
}

.projects-introduction {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0 20px 0;
    align-items: center;
}

.projects-global-information {
    margin: 0 0 40px 0;
}

.projects-description {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    p {
        margin: 0;
        padding: 0;
        font-size: 16px;
        font-weight: 400;
        @media (max-width: 768px) {
            font-size: 15px;
        }
        @media (max-width: 480px) {
            font-size: 14px;
        }
    }
}

.tag-list-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 0 30px 0;
    ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 20px;
        padding: 0;
        flex-wrap: wrap;
        @media (max-width: 768px) {
            gap: 15px;
        }
        @media (max-width: 480px) {
            gap: 10px;
        }
    }
    li {
        border: 1.2px solid $light-main-color;
        padding: 5px 8px;
        font-size: 14px;
        font-weight: 400;
        border-radius: 18px;
        width: fit-content;
        cursor: pointer;
        @media (max-width: 768px) {
            font-size: 13px;
        }
        @media (max-width: 480px) {
            font-size: 12px;
        }
        &:hover {
            border: 1.2px solid $light-secondary-color;
            color: $light-secondary-color;
        }
        &.active {
            border: none;
            background-color: $light-secondary-color;
            color: $light-color;
            font-weight: 500;
        }
    }
}

.projects-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.project-card {
    display: flex;
    flex-direction: column;
    width: 235px;
    height: 350px;
    border-radius: 7px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    @media (max-width: 480px) {
        min-width: 235px;
        max-width: 280px;
        width: 100%;
    }
}

.project-card-picture {
    height: 50%;
    border-radius: 7px 7px 0 0;
    object-fit: cover;
    img {
        width: 100%;
        height: 100%;
        border-radius: 7px 7px 0 0;
    }
}

.project-card-details {
    height: 50%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h3 {
        color: $light-secondary-color;
        margin: 0 0 10px 0;
        padding: 0;
        font-size: 18px;
        font-weight: 600;
    }
    p {
        margin: 0 0 10px 0;
        padding: 0;
        font-size: 14px;
        font-weight: 400;
    }
}

.tags-container {
    display: flex;
    flex-direction: row;
    gap: 6px;
    a {
        display: flex;
    }
    img {
        width: 25px;
        height: 25px;
    }
}

.tags-list {
    list-style: none;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    gap: 6px;
}

.tag-item {
    border: 1px solid $light-main-color;
    padding: 5px 8px;
    font-size: 10px;
    font-weight: 300;
    border-radius: 18px;
    width: fit-content;
}