@import "../utils/variables";

.modal-wrapper {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
}

.modal-overlay {
    z-index: 1;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: $modal-background-color;
    opacity: 0.4;
}

.modal {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 85%;
    height: 85%;
    box-sizing: border-box;
    padding: 5%;
    background-color: white;
    border-radius: 5px;
    z-index: 2;
    overflow-y: auto;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border: 1px solid rgba(99, 99, 99, 0.2);
    &__title {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-bottom: 60px;
        position: relative;
    }
    &__details {
        display: flex;
        flex-direction: column;
    }
}

.modal__title {
    strong {
        font-size: 24px;
        font-weight: 600;
    }
    img {
        width: 20px;
        height: 20px;
        position: absolute;
        right: -20px;
        top: -25px;
        border: none;
        background: none;
        font-size: 12px;
        font-weight: 500;
        cursor: pointer;
    }
}