@import "../utils/variables";

.contact {
    width: 100%;
    height: 475px;
    &__container {
    background-color: $light-main-color;
    color: $light-color;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    }
}

.contact-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 60px;
    box-sizing: border-box;
    gap: 30px;
    width: 100%;
    @media (max-width: 768px) {
        padding: 45px;
    }
    @media (max-width: 480px) {
        padding: 30px;
    }
    h2 {
        margin: 0;
        padding: 0;
        font-size: 40px;
        font-weight: 600;
        text-align: center;
        @media (max-width: 768px) {
            font-size: 30px;
        }
        @media (max-width: 480px) {
            font-size: 25px;
        }
    }
    p {
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        margin: 0;
        padding: 0;
        width: 75%;
        @media (max-width: 768px) {
            font-size: 15px;
        }
        @media (max-width: 480px) {
            font-size: 14px;
        }
    }
    .contact-btn {
        background-color: $light-secondary-color;
        padding: 12px 24px;
        font-family: 'Poppins', sans-serif;
        font-size: 15px;
        font-weight: 500;
        margin: 0;
        cursor: pointer;
        border-radius: 6px;
        color: $light-color;
        border: none;
        @media (max-width: 768px) {
            font-size: 14px;
            padding: 10px 20px;
        }
        @media (max-width: 480px) {
            font-size: 13px;
            padding: 8px 15px;
        }
        &:hover {
            color: $light-secondary-color;
            background-color: $light-background-color;
        }
    }
    .go-back {
        width: 100%;
        display: flex;
        font-family: 'Poppins', sans-serif;
        flex-direction: row;
        justify-content: flex-start;
        button {
            background: none;
            color: $light-color;
            border: none;
            font-size: 14px;
            &:hover {
                color: $light-secondary-color;
            }
        }
    }
    .social-links {
        height: 24px;
        a {
            height: 100%;
        }
        img {
            height: 100%;
            width: 100%;
        }
    }
    .contact-info p {
        width: 100%;
    }
}